class NavbarController {
  constructor() {
    this.name = 'navbar';
    this.toggleToolsMenu = function() {
      var mobileMenuContainer = angular.element('.mobil-menu-container');
      if (!mobileMenuContainer.hasClass('noshow')) {
        angular.element('.mobile-menu-container').addClass('noshow')
      }

    }
  }
}

export default NavbarController;



